(function() {
  'use strict';

  angular
    .module('nqms')
    .factory('modalService', modalServiceFunc);

  function modalServiceFunc($uibModal, $uibModalStack, $log, $parse) {
    return {
      open: function(template, size, fn, dataObj) {
        $uibModal.open({
          templateUrl: template,
          size: size,
          backdrop: 'static',
          animation: true,
          controller: function($scope, $uibModalInstance, callback, data) {
            var vm = this;

            // 预设的表单数据
            vm.formData = data && data.formData;
            // 预设的原始数据
            vm.metaData = data && data.metaData;

            vm.alert = function () {
              $log.info(vm.formData);
            };

            vm.ok = function() {
              callback(vm.formData);
            };
            // vm.parentCtrl = parent_ctrl;

            vm.cancel = function() {
              $uibModalInstance.dismiss('cancel');
            };
          },
          controllerAs: 'vm',
          resolve: {
            callback: function() {
              return fn;
            },
            data: function() {
              if (dataObj) {
                return dataObj;
              }
            }
          }
        });
      },
      close: function(reason) {
        $uibModalStack.dismissAll(reason);
      }
    };
  }
})();

// getUserSysRoles
(function() {
  'use strict';

  angular
    .module('nqms')
    .factory('security', securityFunc);

  function securityFunc(apiHost, $http, $log, $window, toastr, $q, $location, $timeout, $state) {
    var service = {
      currentUser: (function() {
        return getFromSession() || {};
      })(),
      roleList: [],
      changeRole: changeRole,
      removeFromSession: removeFromSession,
      resetUserInfo: resetUserInfo,
      saveAtSession: saveAtSession,
      getFromSession: getFromSession,
      getUserRoleList: getUserRoleList,
      getNavigation: getNavigation,
      addToBreadcrumb: addToBreadcrumb,
      removeFromBreadcrumb: removeFromBreadcrumb,
      login: login,
      logout: logout
    };

    return service;

    // 更换角色
    function changeRole(roleObj) {
      // 根据角色code获取左侧导航数据 同时更新sessionStorage中的用户数据
      getNavigation(roleObj).then(function(response) {
        if (response.data.code === '0') {
          toastr.success('正在切换角色...', '恭喜');
          $state.go('home');

          $timeout(function() {
            $window.location.reload();
          }, 2000);
        } else {
          toastr.error('网络或数据错误，请联系管理员～', '警告');
        }
      });
    }

    // 登录
    function login(formData) {
      var deferred = $q.defer();

      return $http.post(apiHost.system.login, formData).then(function(result) {
        var response_data = result.data.data;
        var user_info;

        if (result.data['code'] == '-1') {
          toastr.error(result.data['msg']);
          return result;
        }

        user_info = {
          user: response_data.user,
          nav: response_data.funs,
          role: response_data.role,
          tab: {}
        };

        service.saveAtSession('userInfo', user_info);

        deferred.resolve(user_info);
        $log.info(result);

        // 登录成功后跳转至首页
        toastr.success('登录成功！');
        $location.path('/home');

        service.currentUser = user_info;

        service.saveAtSession('isLogined', true);
      }, function(error) {
        deferred.reject(error);
      });

      return deferred.promise;
    }

    // 退出系统
    function logout() {
      service.removeFromSession('isLogined');
      service.removeFromSession('userInfo');
      $location.path('/login');
    }

    // 根据用户角色获取导航菜单
    function getNavigation(roleObj) {
      var userInfo = service.getFromSession();

      return $http.get(apiHost.system.getNavigation, {
          params: {
            roleCode: roleObj.roleCode,
            userRoleId : roleObj.seqId
          }
        })
        .then(function(response) {
          // 更新session里的导航数据和角色数据，并置空标签页
          if (response.data.code === '0') {
            userInfo.nav = response.data.data;
            userInfo.role = roleObj;
            userInfo.tab = {};

            saveAtSession('userInfo', userInfo);
          }
          return response;
        });
    }

    // 重置sessionStorage数据
    function resetUserInfo(userInfoObj) {
      $window.sessionStorage.setItem('userInfo', angular.toJson(userInfoObj));
    }

    // 往sessionStorage里存数据
    function saveAtSession(key, value) {
      var to_save_value = value;

      if (angular.isObject(value) || angular.isArray(value)) {
        to_save_value = angular.toJson(value);
      }

      $window.sessionStorage.setItem(key, to_save_value);
    }

    // 从sessionStorage里取数据
    function getFromSession(key) {
      var _key = key;
      // 如果key为空，默认取用户的数据，避免在其他模块多次写userinfo字符串
      if (!key) {
        _key = 'userInfo';
      }

      return angular.fromJson($window.sessionStorage.getItem(_key));
    }

    // 清空sessionStorage 的数据
    function removeFromSession(key) {
      if (typeof key !== 'string') {
        throw 'key必须是字符串';
      }

      $window.sessionStorage.removeItem(key);
    }

    function addToBreadcrumb(key, value) {
      service.currentUser.tab[key] = value;

      resetUserInfo(service.currentUser);
    }

    function removeFromBreadcrumb(key) {
      delete service.currentUser.tab[key];

      resetUserInfo(service.currentUser);
    }

    // 获取当前用户的所有角色
    function getUserRoleList(params) {
      return $http.get(apiHost.system.getRoleList, {
          params: params
        })
        .then(function(response) {
          service.roleList = response.data.data;
          if (response.data.code === '0') {
            return response;
          }
        })
        .catch(function(error) {
          $log.error('获取角色失败', error);
        });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    // 响应拦截器
    .factory('myHttpInterceptor', interceptorFunc);

  /** @ngInject */
  function interceptorFunc($log, $location, $window, $q, $injector) {
    var toastr;
    var isLogined = angular.fromJson($window.sessionStorage.getItem('isLogined'));

    function initToastrIfNeed() {
      if (toastr) {
        return;
      }
      toastr = $injector.get('toastr');
    }

    return {
      // 请求成功
      request: function(config) {
        $log.info('请求结果：' + config.url ,config);
        initToastrIfNeed();

        return config || $q.when(config);
      },

      // 请求失败
      requestError: function(rejection) {
        $log.info('请求失败：' + rejection.statusText ,rejection);
        // 避免出现循环注入的错误
        initToastrIfNeed();
        toastr.error('请检查网络连接或联系服务器管理员！', '请求错误');
        return $q.reject(rejection);
      },

      // 响应成功
      response: function(response) {
        var security = $injector.get('security');
        $log.info('响应结果：' + response.config.url ,response);

        if (response.data.code == -2) {
          security.logout();
        }

        if (response.data.code == -1) {
          initToastrIfNeed();
          toastr.error(response.data.msg + '<br>错误码：-1', '操作不成功！');
        }

        return response || $q.when(response);
      },

      // 响应失败
      responseError: function(rejection) {
        $log.info('响应失败：' + rejection.statusText ,rejection);
        // 避免出现循环注入的错误
        initToastrIfNeed();
        toastr.error('错误码：' + rejection.status + '<br> URL：' + (rejection.config && rejection.config.url || '') + '\n ' + rejection.message + '请检查网络连接或联系服务器管理员！', '没有响应');

        return $q.reject(rejection);
      }
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .factory('deptService', deptServiceFunc);

  /** @ngInject */
  /**
   * [deptServiceFunc 科室数据]
   * @param  {[Object]} $window [全局window对象]
   * @return {[Object]}         [包涵接口和科室列表数据]
   */
  function deptServiceFunc($window, $http, apiHost, $log, toastr, security) {
    var userRole = security.getFromSession()['role'];

    var service = {
      apiHost: apiHost,
      getDeptData: getDeptData
    };

    return service;

    function getDeptData() {

      return $http.get(apiHost.user.getDept, {
        params: {
          deptCode: userRole.deptCode
        }
      })
        .then(getDeptListComplete)
        .catch(getDeptListFailed);

      function getDeptListComplete(response) {
        if (response.data.code === '0') {
          return response.data.data;
        } else {
          $log.error('获取科室数据失败！\n' + response.data.msg);
        }
      }

      function getDeptListFailed(error) {
        $log.error('获取科室数据失败！\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('nqms')
    .directive('loading', loadingFunc);

  /** @ngInject */
  /**
   * [loadingFunc 全局加载动画]
   * @return {[object]} [指令对象]
   */
  function loadingFunc($http, $log) {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/common/loading/loading.tpl.html',
      link: loadingLinkFunc,
      scope: {},
      controller: function ($window, $log) {
        var vm = this;

        $log.info('windowWidth:' ,$window);

        vm.loadingWidth = $window.innerWidth - 200;
      },
      controllerAs: 'vm'
    };

    return directive;

    function loadingLinkFunc(scope, elem, attrs) {
      $log.info('scope', scope);
      scope.isLoading = function() {
        return $http.pendingRequests.length > 0;
      };

      scope.$watch(scope.isLoading, function(v) {
        // $(elem).show();
        if (v) {
          elem[0].setAttribute('style', 'display: block');
        } else {
          elem[0].setAttribute('style', 'display: none');
        }
      });
    }
  }



})();

